.center-child {
  display: flex;
  justify-content: center;
  align-items: center;
}

.vis-time-axis .vis-text.vis-major {
  font-weight: 500;
  padding-left: 10px;
}

.vis-time-axis .vis-text.vis-minor {
  text-align: center;
  font-size: 90%;
}

.vis-content .vis-labelset .vis-label .vis-inner {
  text-align: center;
  font-size: 80%;
  display: block;
}

.vis-item.vis-dot {
  border-color: #97B0F8;
}

.vis-item.vis-dot.vis-selected {
  border-color: #97B0F8;
  background-color: #97B0F8;
}

.vis-item {
  border-color: #97B0F8;
}

.vis-item.vis-selected {
  border-color: #97B0F8;
  background-color: #41a0ff !important;
  z-index: 2;
}

.vis-item.vis-range, .vis-item.vis-box {
  background-color: #1978d8;
  border: none !important;
  border-radius: 3px !important;
  border-style: none !important;
  cursor: pointer !important;
}

.vis-item.vis-range *, .vis-item.vis-box * {
  cursor: pointer;
}

.vis-item .vis-item-content {
  padding: 2px 10px !important;
  font-size: 80% !important;
  color: white !important;
  font-weight: 500 !important;
}

.vis-item.vis-background.negativeTimelineBackground {
  background-color: rgba(255, 0, 0, 0.2);
}

.vis-background > .vis-custom-time {
  background-color: #FF7F6E;
}

.divider[data-content]:after {
  font-size: 80%;
}

.panel, .card, .tile {
  border-radius: 15px;
}

:root {
  font-size: 20px;
}
* {
  box-sizing: border-box;
}
*::-webkit-scrollbar-track {
  background: #bcc3ce;
}
*::-webkit-scrollbar-thumb {
  background: #66758c;
}

html, body {
  margin: 0;
  padding: 0;
  height: 100%;
}

details summary {
  cursor: pointer;
}

body::-webkit-scrollbar {
  width: 0.4rem;
}
body.drawer-open {
  height: 100vh;
  overflow-y: hidden;
}

.h1, .h2, .h3, .h4, .h5, .h6 {
  font-weight: 500;
}

.h6, h6 {
  font-size: 0.8rem;
}

#root {
  display: flex;
  height: 100%;
}
@media only screen and (max-width: 550px) {
  #root {
    flex-direction: column;
  }
}
@media only screen and (min-width: 550px) {
  #root {
    flex-direction: row;
  }
}
#root #view {
  width: 100%;
  flex-direction: column;
  padding: 25px;
  margin-top: 4rem;
}
#root #viewcontent {
  flex-grow: 1;
}

#loader {
  position: fixed;
  width: 100vw;
  height: 100vh;
}
#loader:after {
  height: 2rem !important;
  width: 2rem !important;
}

#drawer .tooltip:after {
  position: fixed;
  transform: initial;
  bottom: initial;
  left: 2.8rem;
}

#top-modal {
  z-index: 1001;
}
#top-modal textarea {
  min-height: 5rem;
}
#top-modal .modal-container {
  max-height: 95vh;
}

.tab-item a, .menu-item a {
  cursor: pointer;
}

.link-btn-hover:hover {
  background: #f1f1fc !important;
  color: #5755d9 !important;
  border-radius: 0.1rem !important;
  cursor: pointer !important;
}

.profile-badge {
  display: flex;
  align-items: center;
}
.profile-badge .profile-info {
  padding: 0px 10px;
  color: black;
  font-size: 80%;
  line-height: 0.7rem;
}
.profile-badge .profile-info p {
  margin: 0;
}

td.min-width, th.min-width {
  width: 1%;
  text-align: center !important;
}

td .expandable .collapse {
  display: none;
}